import React from 'react';
import {  Table, Tag } from 'antd';
import type { ColumnsType } from 'antd/es/table';
import { DataType } from '../../data/data';


const columns: ColumnsType<DataType> = [
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: ' ',
    dataIndex: 'type',
    key: 'type',
  },
  {
    title: '',
    dataIndex: 'address',
    key: 'address',
  },
  {
    title: '.',
    dataIndex: 'unit',
    key: 'unit',
  },
  {
    title: '.',
    dataIndex: 'itemNumber',
    key: 'itemNumber',
  },
  {
    title: '',
    key: 'tags',
    dataIndex: 'tag',
    render: (_, { tag }) => (
      <Tag key={tag}>
        {tag.toUpperCase()}
      </Tag>
    ),
  },
  {
    title: ' ',
    dataIndex: 'created_at',
    key: 'created_at',
  },
  {
    title: ' ',
    dataIndex: 'updated_at',
    key: 'updated_at',
  },
  {
    title: ' ',
    dataIndex: 'audit_at',
    key: 'audit_at',
  },
];

const rowSelection = {
    onChange: (selectedRowKeys: React.Key[], selectedRows: DataType[]) => {

    },
    getCheckboxProps: (record: DataType) => ({
      disabled: record.name === 'Disabled User',
      name: record.name,
    }),
  };

type Props = {
  searchData?: DataType[]
  };

const TableApp: React.FC<Props> = ({searchData}) => {


  return (
    <Table columns={columns} dataSource={searchData} rowSelection={{ ...rowSelection,}} pagination={{total: searchData?.length, hideOnSinglePage: true, showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`}}/>
  )

}


export default TableApp;